;\n            }\n            \n            .thumbsBlock {\n                display: table-cell;\n                border-radius: 2px;\n                vertical-align: top;\n                display: inline-block;\n            }\n            /*______________________________ CONTROLS __________________________*/\n            \n            .divcontrols {\n                margin: 0px auto 0px auto;\n                width: 100%;\n                height: 25px;\n                position: absolute;\n                visibility: visible;\n                bottom: 0px;\n                left: 0px;\n            }\n            \n            .divcontrolsProgress {\n                margin: 0px auto 0px auto;\n                width: 100%;\n                height: 8px;\n                position: absolute;\n                visibility: visible;\n                bottom: 25px;\n                left: 0px;\n            }\n            \n            .divconeontrolLeft {\n                float: left;\n            }\n            \n            .divconeontrolRight {\n                float: right;\n            }\n            \n            .divconeontrolIcon {\n                width: 30px;\n            }\n            \n            .divconeontrolVBar {\n                width: 60px;\n            }\n            \n            .divonecontrol {\n                height: 25px;\n                padding-top: 2px;\n                padding-right: 1px;\n            }\n            \n            .divTitle,\n            .divcontrols,\n            .control,\n            .divonecontrol {\n                display: inline-block;\n                vertical-align: top;\n            }\n            /* this rule is for IE7 and below only\n put it in a Conditional comment without the !ie7 hack if you wish! */\n            \n            .divcontrols,\n            .control,\n            .divonecontrol {\n                display: inline !ie7;\n                margin: 0 2px !ie7;\n            }\n            \n            .controlText {\n                padding-top: 2px;\n                font-size: 0.75em;\n                white-space: nowrap;\n            }\n            \n            .divInfo {\n                z-index: 2147483647;\n                border-radius: 5px;\n                top: 10%;\n                width: 80%;\n                position: absolute;\n                left: 10%;\n            }\n            \n            .divBigPlayBtn {\n                font-size: 3.2em;\n                z-index: 2147483647;\n                border-radius: 5px;\n                top: 35%;\n                position: absolute;\n                left: 45%;\n                background-color: rgba(0, 0, 0, 0.5);\n                border-radius: 2%;\n                padding: 2%;\n            }\n            \n            .divBigPlayBtn:hover {\n                background: #333;\n            }\n            \n            .divIconBtn {\n                position: relative;\n                text-overflow: ellipsis;\n                overflow: hidden;\n                white-space: nowrap;\n                display: inline-block;\n                cursor: pointer;\n                opacity: 0.8;\n            }\n            \n            .divIconBtn:hover {\n                opacity: 1.0;\n                background: #333;\n            }\n            \n            .divTspanitleSeparator {\n                position: relative;\n                visibility: visible;\n                text-overflow: ellipsis;\n                overflow: hidden;\n                margin: 0px;\n                white-space: nowrap;\n                display: inline-block;\n                max-width: 766px;\n                font-size: 12px;\n                padding: 10px;\n            }\n            /*_________________________  Progress  ________________________*/\n            \n            .progressbar {\n                position: relative;\n                visibility: visible;\n                width: 100%;\n                display: inline-block;\n  